import React from "react";
import { graphql } from "gatsby";
import Img from "gatsby-image";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faLinkedinIn,
	faTwitter,
	faFacebook,
} from "@fortawesome/free-brands-svg-icons";

import BlogHeader from "../components/blog/header";
import Layout from "./layout";
import styles from "./styles/article.module.scss";

const Article = ({ data }) => {
	const article = data.markdownRemark;
	const featuredImage =
		article.frontmatter.featuredImage.childImageSharp.fluid;

	return (
		<Layout>
			<BlogHeader />
			<article className={styles.article}>
				<Img fluid={featuredImage} className={styles.featuredImage} />
				<div className={styles.container}>
					<div className={styles.breadcrumbs}></div>
					<div className={styles.article__content}>
						<h1 className={styles.article__title}>
							{article.frontmatter.title}
						</h1>
						<p className={styles.article__date}>
							{article.frontmatter.date}
						</p>
						<div className={styles.article__share}>
							<p>Share</p>
							<a
								className={[
									styles.social__icon,
									styles.social__iconTwitter,
								].join(" ")}
								href={`https://twitter.com/intent/tweet/?text=${article.frontmatter.title}&url=https://www.chirinosky.com/blog${article.fields.slug}`}
								target="_blank"
								rel="noopener noreferrer"
							>
								<FontAwesomeIcon icon={faTwitter} />
							</a>
							<a
								className={[
									styles.social__icon,
									styles.social__iconFacebook,
								].join(" ")}
								href={`https://www.facebook.com/sharer.php?u=https://www.chirinosky.com/blog${article.fields.slug}`}
								target="_blank"
								rel="noopener noreferrer"
							>
								<FontAwesomeIcon icon={faFacebook} />
							</a>
							<a
								className={[
									styles.social__icon,
									styles.social__iconLinkedin,
								].join(" ")}
								href={`https://www.linkedin.com/shareArticle/?mini=true&url=https://www.chirinosky.com/blog${article.fields.slug}&title=${article.frontmatter.title}&summary=${article.frontmatter.excerpt}&source=https://www.chirinosky.com/blog`}
								target="_blank"
								rel="noopener noreferrer"
							>
								<FontAwesomeIcon icon={faLinkedinIn} />
							</a>
						</div>
						<div
							className={styles.article__copy}
							dangerouslySetInnerHTML={{ __html: article.html }}
						></div>
					</div>
				</div>
			</article>
		</Layout>
	);
};

export default Article;

export const query = graphql`
	query($slug: String!) {
		markdownRemark(fields: { slug: { eq: $slug } }) {
			fields {
				slug
			}
			html
			frontmatter {
				title
				date(formatString: "MMMM DD, YYYY")
				featuredImage {
					childImageSharp {
						fluid(maxWidth: 1440) {
							...GatsbyImageSharpFluid
						}
					}
				}
			}
		}
	}
`;
